import React, {Component} from 'react';


export default class Grid extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: props.gridArray,
            countRows: 10,
            countColumn: 32,
        };
    }

    makeGrid(){
        let grid = [];

        for(let i = 0;i < this.state.countRows;i++){
            grid[i] = [];
            for(let j = 0;j < this.state.countColumn;j++){
                grid.push(
                    <div key={i} className="cell">
                        {this.state.grid[i][j]}
                    </div>
                );
            }
        }

        return grid;
    }

    render(){
        let grid = this.makeGrid();
        return(
            <div className="Grid">
                {
                   grid
                }
            </div>
        );
    }
}